.ProjectCard {
  border-radius: 40px !important;
  background-color: black;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22);
  transition: 0.2s;
}

.ProjectCard__image {
  border-radius: 40px !important;
}

.ProjectCard__overlay {
  position: absolute;
  border-radius: 40px !important;
  background: rgba(0, 0, 0, 0.6) !important;
  color: #ffff;
  opacity: 0;
  transition: 0.25s;
}

.ProjectCard__overlay > * {
  transform: translateY(30px);
  transition: 0.5s;
}

.ProjectCard__overlay:hover > * {
  transform: translateY(0);
}

.ProjectCard__overlay:hover {
  opacity: 1;
}

.ProjectCard__title {
  font-size: 2em;
  font-weight: bold;
}

.ProjectCard__description {
  font-size: 1.25em;
  margin-top: 0.25em;
}
.ProjectCard__gitlogo {
  height: 5vh;
}
.ProjectCard :hover {
  cursor: pointer !important;
}

.ProjectCard__gitlogo:hover {
  color: royalblue;
}

.ProjectCard__logos {
  display: flex;
  position: relative;
  flex-direction: row !important;
  margin-right: -100px;
  top: 20%;
}

.ProjectCard__webIcon {
  height: 5vh;
  width: 16%;
}
.ProjectCard__webIcon:hover {
  color: royalblue;
}
