.Intro {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.bg-custom-1 {
  background-color: #483f72 !important;
  margin-top: 18%;
  border-radius: 40px !important;
}

.Intro__profilepic {
  width: 200px;
  height: 200px;
  margin-top: 2px;
  align-self: center;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22) !important;

  display: inline-block;
  vertical-align: middle;
  border: 1px solid #03bfcb;
  border-radius: 50%;
  padding: 7px;
}

.Intro__Skills {
  margin-top: 180px;
}

.Intro__written {
  display: flex;
  justify-content: center;
  margin: 2.5rem;
}

.Intro__skill {
  position: relative;
  font-size: 24px;
  top: 30%;
  left: 2%;
}

